<template>
	<div ref="bmap" class="mod_map"></div>
</template>

<script>
export default {
	props: {
		address: {
			type: String,
			default: ""
		}
	},
	data() {
		return {
			city: ""
		}
	},
	mounted() {
		var map = new window.BMap.Map(this.$refs.bmap) // Map
		map.centerAndZoom(this.getCity(this.address), 11) // 
		map.enableScrollWheelZoom(true) // 
	},
	methods: {
		getCity(address) {
			if (!address) {
				return ""
			} else if (~this.address.indexOf("") || ~this.address.indexOf("")) {
				return ~this.address.indexOf("") ?
					this.address.substring(0, this.address.indexOf("") + 1) :
					this.address.substring(0, this.address.indexOf("") + 1)
			} else if (~this.address.indexOf("")) {
				return this.address.substring(0, this.address.indexOf("") + 1)
			} else if (~this.address.indexOf("")) {
				return this.address.substring(0, this.address.indexOf("") + 1)
			} else {
				this.$message({
					message: "",
					type: "warning"
				})
				return ""
			}
		}
	}
}
</script>

<style lang="stylus">
.mod_map
	width 100%
	height 200px
</style>



